export function copyToClipboard(text: string) {
  if (text.trim().length) {
    navigator.clipboard
      .writeText(text.trim())
      .then(() => {
        alert('     !');
      })
      .catch(err => {
        alert('      !');
        throw new Error(err);
      });
  } else {
    alert('      !');
  }
}
